<template>
    <div class="hero">
	<h1>Menu</h1>
  <br>
	<img v-bind:src="imagePath"/>
    </div>
</template>

<script>
 export default {
     name: 'Menu',
     data () {
	 return {
	     imagePath: '/static/images/menu1.jpg'
	 }
     }
 }
</script>

<style scoped>
 html, body {
     padding: 0px;
     margin: 0px;
 }
 .hero {
     text-align: center;
 }
 h1 {
     font-size: 2.5em;
     letter-spacing: .2rem;
     color: #999;
     margin-bottom: 2px;
 }
 img {
     width: 100%;
 }
</style>


